import React from 'react'

import { SelectControlFlat } from '../atoms/SelectControlFlat'
import { FCompanyBranchFragment } from '../graph/generated'
import { ICompanyBranchPickerImpl } from '../organisms/CompanyBranchPicker'
import { Colors } from '../styling'

export const CompanyBranchFlatSelect: React.FC<ICompanyBranchPickerImpl> = ({
  branches,
  selectedBranch,
  selectedBranches,
  textColor = Colors.black,
  ...props
}) => (
  <SelectControlFlat<FCompanyBranchFragment>
    items={branches}
    selectedItem={selectedBranch}
    selectedItems={selectedBranches}
    textColor={textColor}
    formatItem={item => <span title={item.brand.name}>{item.name}</span>}
    {...props}
  />
)
