import { History } from 'history'
import React from 'react'
import { Link } from 'react-router-dom'

import { NoAccess } from '../atoms/NoAccess'
import {
  IPageLayoutGridItemProps,
  PageLayoutGridItem,
} from '../atoms/PageLayoutGridItem'
import { Sizer } from '../atoms/Sizer'
import { useAccessGuard } from '../hooks/useAccessGuard'
import { useUserModel } from '../hooks/useUserModel'
import { Spacing } from '../styling'

interface IPageLayoutItemProps extends IPageLayoutGridItemProps {
  children: ReactNode
  route?: History.LocationDescriptor<any>
  canQuery?: string | string[]
  canMutate?: string | string[]
  canAccessWithSingleBranch?: boolean
}

const EMPTY_ARRAY = []

export const PageLayoutItem: React.FC<IPageLayoutItemProps> = ({
  route,
  children,
  canQuery = EMPTY_ARRAY,
  canMutate = EMPTY_ARRAY,
  canAccessWithSingleBranch = true,
  ...gridItemProps
}) => {
  const userModel = useUserModel()

  const queries = normalizeAccess(canQuery)
  const mutations = normalizeAccess(canMutate)
  const hasAccess = useAccessGuard(queries, mutations)

  if (!hasAccess) {
    return (
      <NoAccess>{`No access to ${
        queries.length ? `queries ${queries.join(',')} ` : ''
      }${
        mutations.length ? `mutations ${mutations.join(',')}` : ''
      }`}</NoAccess>
    )
  }

  if (!canAccessWithSingleBranch && userModel.hasSingleBranch) {
    return <NoAccess>No access to single branch account</NoAccess>
  }

  const layoutItem = route ? (
    <Link to={route}>{children}</Link>
  ) : (
    <>{children}</>
  )

  return (
    <PageLayoutGridItem {...gridItemProps}>
      <Sizer height={Spacing.DashboardButtonHeight}>{layoutItem}</Sizer>
    </PageLayoutGridItem>
  )
}

function normalizeAccess(access: string | string[]) {
  return Array.isArray(access) ? access : [access]
}
