import { Error, SvgIconComponent } from '@material-ui/icons'
import { cn } from '@speedlo/ui'
import React from 'react'
import styled from 'styled-components'

import { mediaMobile } from '../app/Responsive'
import { logError } from '../helpers/logger'
import { Colors, Spacing } from '../styling'
import { useFormGridCheck } from './FormGrid'
import { Icon } from './Icon'

interface IProps {
  icon?: SvgIconComponent
  label?: string | ReactNode
  labelWidth?: number
  labelWidthUnits?: string
  labelMargin?: string
  labelSmallCaps?: boolean
  className?: string
}

const StyledIconLine = styled.div<IProps>`
  display: flex;

  div.description {
    margin: ${({ labelMargin }) => labelMargin};
    min-width: ${({ labelWidth, labelWidthUnits }) =>
      `${labelWidth}${labelWidthUnits}`};
    flex: 0 0 auto;
    text-align: right;
  }

  div.contents {
    margin-left: 0.5rem;
    align-self: stretch;
    flex: 1 1 auto;
  }

  div.label {
    padding-top: 0.2rem;
    display: inline-block;
    &.smallCaps {
      font-variant-caps: all-petite-caps;
    }
  }
`

const StyledGridDescription = styled.div<IProps>`
  grid-column: desc;
  margin: ${({ labelMargin }) => labelMargin};

  div.label {
    padding-top: 0.2rem;
    display: inline-block;
    &.smallCaps {
      font-variant-caps: all-petite-caps;
    }
  }

  ${mediaMobile`
  grid-column: content;
  justify-self: start;
  margin: ${({ labelMargin }) => labelMargin.replace('auto', '0.7rem')};
  `};
`

const StyledGridContent = styled.div<IProps>`
  grid-column: content;
  margin-left: 0.5rem;
`

export const IconLine: React.FC<IProps> = ({
  icon,
  label,
  labelWidth = 0,
  labelWidthUnits = Spacing.DefaultSizeUnit,
  labelMargin = '0.5rem 0.2rem 0 auto',
  labelSmallCaps = true,
  children,
}) => {
  let iconComponent = icon
  if (typeof icon === 'string') {
    logError(
      `IconLine: Do not use icon prop as string! Replace "${icon}" with the one imported from @material-ui/icons.`,
    )
    iconComponent = Error
  }
  const isInFormGrid = useFormGridCheck()

  const descriptionPart = (
    <>
      <Icon icon={iconComponent} color={Colors.greyDark} />
      {label ? (
        <div className={cn('label', { smallCaps: labelSmallCaps })}>
          {label}
        </div>
      ) : null}
    </>
  )

  return isInFormGrid ? (
    <>
      <StyledGridDescription labelMargin={labelMargin}>
        {descriptionPart}
      </StyledGridDescription>
      <StyledGridContent>{children}</StyledGridContent>
    </>
  ) : (
    <StyledIconLine
      labelWidth={labelWidth}
      labelWidthUnits={labelWidthUnits}
      labelMargin={labelMargin}
    >
      <div className="description">{descriptionPart}</div>
      <div className="contents">{children}</div>
    </StyledIconLine>
  )
}
