import { Tooltip } from '@material-ui/core'
import { PanTool } from '@material-ui/icons'
import React from 'react'

interface IProps {
  children: string
}

export const NoAccess: React.FC<IProps> = ({ children }) =>
  process.env.REACT_APP_STAGE !== 'prod' ? (
    <Tooltip title={children}>
      <PanTool />
    </Tooltip>
  ) : null
