import { Trans } from '@lingui/macro'
import { cn } from '@speedlo/ui'
import React from 'react'
import styled from 'styled-components'

import { RM } from '../tools/ramda'
import { Colors } from '../styling'

export interface ISelectControlFlatProps<ItemType = unknown> {
  items: RoA<ItemType>
  selectedItem?: Maybe<ItemType>
  formatItem?: (item: ItemType) => string | ReactNode
  onSingleSelection?(selected: Maybe<ItemType>): void
  selectedItems?: RoA<ItemType>
  onMultiSelection?(selected: RoA<ItemType>): void
  isMulti?: boolean
  isClearable?: boolean
  isInvalid?: boolean
  disabled?: boolean
  textColor?: string
}

const StyledSelectControlFlat = styled.div`
  ${(props: { textColor?: string }) => ``}

  color: ${({ textColor }) => (textColor ? textColor : 'inherit')};

  &.interactive {
    cursor: pointer;
  }

  &.invalid {
    border: 1px red solid;
  }

  > div {
    padding: 0.2rem 0.3rem;
    margin: 0.3rem 0.3rem;
    display: inline-block;
    border: 1px dotted ${Colors.greyMiddle};
  }

  > div.active {
    font-weight: bolder;
    text-decoration: underline;
  }

  > div.hover {
    text-decoration: underline;
  }
`

export const SelectControlFlat = <ItemType extends any>(
  props: ISelectControlFlatProps<ItemType>,
) => {
  const {
    items,
    formatItem = RM.prop('displayName'),
    isMulti = false,
    isClearable = false,
    isInvalid = false,
    disabled = false,
    selectedItem,
    selectedItems,
    onSingleSelection,
    onMultiSelection,
    textColor,
  } = props

  const hasSingleItem = items.length === 1

  const hasSelection = () => {
    if (isMulti) {
      return Array.isArray(selectedItems) && selectedItems.length > 0
    }
    return selectedItem !== null && selectedItem !== undefined
  }

  const isItemSelected = (item: ItemType) => {
    if (items.length === 1) {
      // with one item there no selection happening
      return false
    }
    if (isMulti && Array.isArray(selectedItems)) {
      return RM.contains(item, selectedItems)
    }
    return RM.equals(selectedItem, item)
  }

  const updateSelection = (item: Maybe<ItemType>) => {
    if (hasSingleItem || disabled) {
      return
    }
    if (item === null) {
      if (isMulti && onMultiSelection) {
        onMultiSelection([])
      } else if (onSingleSelection) {
        onSingleSelection(null)
      }
    } else if (isMulti && onMultiSelection) {
      if (isItemSelected(item)) {
        onMultiSelection(
          Array.isArray(selectedItems)
            ? RM.without([item], selectedItems)
            : [item],
        )
      } else {
        onMultiSelection(
          Array.isArray(selectedItems) ? RM.concat(selectedItems, [item]) : [],
        )
      }
    } else if (onSingleSelection) {
      onSingleSelection(item)
    }
  }
  return (
    <StyledSelectControlFlat
      className={cn({
        invalid: isInvalid,
        interactive: !hasSingleItem || disabled,
      })}
      textColor={textColor}
    >
      {isClearable && (
        <div
          className={cn({ active: !hasSelection() })}
          onClick={() => {
            updateSelection(null)
          }}
        >
          <Trans id="optionselector.clear-filter">All</Trans>
        </div>
      )}
      {items.map((item, idx) => {
        // @ts-ignore Have to use any in generics due to parsing error
        const formatted = formatItem(item)
        return (
          <div
            key={idx}
            className={cn({ active: isItemSelected(item) })}
            onClick={() => {
              updateSelection(item)
            }}
          >
            {formatted}
          </div>
        )
      })}
    </StyledSelectControlFlat>
  )
}
