import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { BranchPreferencesRoutes } from '../../branchpreferences/routes/BranchPreferencesRoutes'
import { SettingsPage } from '../../dashboard/pages/SettingsDashboardPage'
import {
  ROUTE_SETTINGS,
  ROUTE_SETTINGS_HOURS,
  ROUTE_SETTINGS_MENU,
  ROUTE_SETTINGS_PREFS,
  ROUTE_SETTINGS_USER,
  ROUTE_SETTINGS_ZONES,
} from '../settings.routes'

const BusinessHoursRouting = React.lazy(() =>
  import('../../businesshours/routes/BusinessHoursRouting'),
)

const ZoneMenuRouting = React.lazy(() =>
  import('../../../segments/zones/routes/ZoneMenuRouting'),
)

const MenuRouting = React.lazy(() =>
  import('../../../segments/menu/routes/MenuRouting'),
)

const UserMenuPage = React.lazy(() => import('../pages/UserMenuPage'))

export const SettingsRouting: React.FC = () => {
  return (
    <Switch>
      <SecureRoute path={ROUTE_SETTINGS} exact component={SettingsPage} />
      <SecureRoute path={ROUTE_SETTINGS_MENU} component={MenuRouting} />
      <SecureRoute path={ROUTE_SETTINGS_ZONES} component={ZoneMenuRouting} />
      <SecureRoute path={ROUTE_SETTINGS_USER} component={UserMenuPage} />
      <SecureRoute
        path={ROUTE_SETTINGS_HOURS}
        component={BusinessHoursRouting}
      />
      <SecureRoute
        path={ROUTE_SETTINGS_PREFS}
        component={BranchPreferencesRoutes}
      />
      <Route component={NotFoundPage} />
    </Switch>
  )
}

export default SettingsRouting
