import React from 'react'

import { CompanyBranch } from '../atoms/CompanyBranch'
import { SelectControl, TSelectControlProps } from '../atoms/SelectControl'
import { FCompanyBranchFragment } from '../graph/generated'
import { ARRAY_EMPTY } from '../helpers/form'
import { i18n } from '../i18n'
import { ICompanyBranchPickerImpl } from '../organisms/CompanyBranchPicker'
import { Spacing } from '../styling'
import { RM } from '../tools/ramda'

type TProps = NoChildren &
  ICompanyBranchPickerImpl &
  TSelectControlProps<FCompanyBranchFragment>

export type TCompanyBranchDropdownProps = Pick<
  TSelectControlProps<FCompanyBranchFragment>,
  'placeholder' | 'width'
>

export const CompanyBranchDropdown: React.FC<TProps> = ({
  branches,
  selectedBranch,
  onSingleSelection,
  selectedBranches,
  onMultiSelection,
  ...props
}) => (
  <SelectControl<FCompanyBranchFragment>
    value={props.isMulti ? selectedBranches : selectedBranch}
    placeholder={
      props.isMulti ? i18n.t`Filter branches...` : i18n.t`Select a branch`
    }
    maxHeight={Spacing.ButtonHeight}
    options={branches as FCompanyBranchFragment[]}
    getOptionValue={RM.prop('id')}
    formatOptionLabel={branch => <CompanyBranch branch={branch} />}
    filterOption={(branch, rawInput) =>
      branch.data.name
        .toLocaleLowerCase()
        .includes(rawInput.toLocaleLowerCase())
    }
    closeMenuOnSelect={!props.isMulti}
    onChange={selection => {
      if (props.isMulti && onMultiSelection) {
        onMultiSelection(
          Array.isArray(selection) ? selection : (ARRAY_EMPTY as any),
        )
      }
      if (!props.isMulti && onSingleSelection) {
        if (Array.isArray(selection)) {
          onSingleSelection(RM.head(selection) || null)
        } else {
          onSingleSelection((selection as FCompanyBranchFragment) || null)
        }
      }
    }}
    {...props}
  />
)
