import React from 'react'

import { CursorContainer } from '../atoms/CursorContainer'
import { FCompanyBranchFragment } from '../graph/generated'

interface ICompanyBranchProps {
  branch: FCompanyBranchFragment
  onClick?: () => void
  short?: boolean
  showBrand?: boolean
}

export const CompanyBranch: React.FC<ICompanyBranchProps> = ({
  branch,
  onClick,
  short = false,
  showBrand = true,
}) => (
  <CursorContainer cursor="pointer" onClick={onClick}>
    {short ? (
      branch.name.substr(0, 8)
    ) : (
      <>
        <span>{branch.name}</span>
        {showBrand && <span>{` [${branch.brand.name}]`}</span>}
      </>
    )}
  </CursorContainer>
)
