import { StoreMallDirectory } from '@material-ui/icons'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { useRoot } from '../../../atoms/RootProvider'
import { useAutorunWhenSingleBranch } from '../../../hooks/useAutorunWhenSingleBranch'
import { useQueryParams } from '../../../hooks/useQueryParams'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'

interface IProps {
  isMulti?: boolean
  width?: number
}

export const SettingsCompanyBranchPicker: React.FC<IProps> = ({
  isMulti = false,
  width,
}) => {
  const { settings, user } = useRoot()

  useAutorunWhenSingleBranch(branch => {
    settings.selectBranch(branch.id)
  })

  const { getQueryParam } = useQueryParams()
  const branchId = getQueryParam('branchId')

  // Change to branch specified in query params
  React.useEffect(() => {
    if (branchId) {
      settings.selectBranch(branchId)
    }
  }, [branchId, settings])

  return useObserver(() => {
    if (user.hasSingleBranch) {
      return null
    }
    return (
      <IconLine icon={StoreMallDirectory}>
        <CompanyBranchPicker
          selectedBranches={settings.branchId ? [settings.branchId] : undefined}
          onSelectionChange={([branch]) => settings.selectBranch(branch)}
          isMulti={isMulti}
          isClearable={false}
          getDropdownProps={() => ({ width })}
          hideWhenSingleBranch={true}
        />
      </IconLine>
    )
  })
}
