import { useXField, useXFormState, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { StyledCheckbox } from '../../atoms/StyledCheckbox'

type TProps = NoChildren &
  Omit<React.InputHTMLAttributes<never>, 'name'> & {
    fieldName: XFieldName
    label?: string
  }

export type FormInputCheckboxProps = TProps

export const FormCheckbox: React.FC<TProps> = ({
  fieldName,
  label,
  children,
  ...inputProps
}) => {
  const field = useXField<any>(fieldName)
  const formState = useXFormState()
  return useObserver(() => (
    <StyledCheckbox>
      <input
        type="checkbox"
        id={field.name}
        name={field.name}
        checked={field.value === true}
        onChange={() => field.setValue(!field.value)}
        disabled={formState.isSubmitting}
        {...inputProps}
      />
      {label && <label htmlFor={field.name}>{label}</label>}
    </StyledCheckbox>
  ))
}
