import React from 'react'
import styled from 'styled-components/macro'

import { mediaMobile } from '../app/Responsive'

const StyledFormGrid = styled.div`
  display: grid;
  grid-template-columns: 0.1fr [desc] auto [content] 1fr;
  grid-template-areas: '. desc content';
  grid-auto-flow: row dense;

  ${mediaMobile`
    grid-template-columns: 1fr;
    grid-template-areas:
      'content';
  `};
`

interface IProps {}
let formGridCounter = 0
const formGridContext = React.createContext(formGridCounter)

export const FormGrid: React.FC<IProps> = props => {
  return (
    <StyledFormGrid>
      <formGridContext.Provider value={++formGridCounter}>
        {props.children}
      </formGridContext.Provider>
    </StyledFormGrid>
  )
}

export function useFormGridCheck() {
  return React.useContext(formGridContext) > 0
}
