import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { ROUTE_SETTINGS_PREFS } from '../../settings/settings.routes'
import { BranchPreferencesPage } from '../pages/BranchPreferencesPage'

interface IProps {}

export const BranchPreferencesRoutes: React.FC<IProps> = () => (
  <Switch>
    <SecureRoute
      exact
      path={ROUTE_SETTINGS_PREFS}
      component={BranchPreferencesPage}
    />
    <Route component={NotFoundPage} />
  </Switch>
)
