import { History } from 'history'
import React from 'react'

import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { ITouchButtonProps, TouchButton } from '../../../molecules/TouchButton'

interface IProps extends ITouchButtonProps {
  row: number
  column: number
  route?: History.LocationDescriptor<any>
  canQuery?: string | string[]
  canMutate?: string | string[]
  canAccessWithSingleBranch?: boolean
}

export type IDashboardButtonProps = IProps

export const DashboardButton: React.FC<IProps> = ({
  row,
  column,
  route,
  canQuery,
  canMutate,
  canAccessWithSingleBranch,
  ...buttonProps
}) => {
  return (
    <PageLayoutItem
      gridColumnSpan={2}
      gridColumn={(column - 1) * 2 + 1}
      gridRow={row}
      route={route}
      canQuery={canQuery}
      canMutate={canMutate}
      canAccessWithSingleBranch={canAccessWithSingleBranch}
    >
      <TouchButton {...buttonProps} />
    </PageLayoutItem>
  )
}
